import React, { Component } from "react";
import Layout from "../components/Layouts";
import { Router, Route } from "react-router-dom";
import { asyncComponent, getPrefixedUrl } from "../components/Commons/helper";
import { Examples } from "../.cache/template";
import Entries from "./config";
import camelCase from "camelcase";

import createHistory from 'history/createBrowserHistory'
const history = createHistory()

const AsyncHome = asyncComponent(() => import("../components/Home"));
const AsyncExample = asyncComponent(() => import("../components/Example"));

export default class BHPRouter extends Component {
  generateRoute(menuItem, index) {
    let { linkTo } = menuItem;
    let componentName = camelCase(linkTo.match(/([^/]*)\/*$/)[1]);
    componentName = linkTo === "/" ? "/" : componentName;
    console.log(componentName, Examples[componentName], getPrefixedUrl(linkTo));
    return (
      <Route
        key={index}
        path={getPrefixedUrl(linkTo)}
        render={props => (
          <AsyncExample config={Examples[componentName]} {...props} />
        )}
      />
    );
  }

  render() {
    return (
      <Router history={history}>
        <Layout routerListener={history}>
          {Entries.map((entry, index) => {
            let { subMenu } = entry;
            if (subMenu) {
              return subMenu.map((menuItem, index) =>
                this.generateRoute(menuItem, index)
              );
            } else {
              return this.generateRoute(entry, index);
            }
          })}
          <Route exact path={getPrefixedUrl("/")} component={AsyncHome} />
        </Layout>
      </Router>
    );
  }
}



