import React from "react";
import classnames from "classnames";
import { withRouter } from "react-router";
import { Link } from "react-router-dom";
import {  getPrefixedUrl } from "../Commons/helper";

import { MDIcon } from "bhp-ui-react";
import { BHPLogo } from "bhp-ui-react";
import "./styles.css";
import Entries from "../../router/config"

const SideBarLink = ({ name, linkTo, mdiName, showToggle, onClick, ...rest }) => {
  if (linkTo) {
    return (
      <Link key={name} className={"link"} to={getPrefixedUrl(linkTo)} onClick={onClick}>
        <MDIcon className={"linkIcon"} name={mdiName} />
        {name}
        {showToggle && <MDIcon className={"toggle"} name={showToggle} />}
      </Link>
    );
  } else {
    return (
      <div key={name} className={"link"} onClick={onClick}>
        <MDIcon className={"linkIcon"} name={mdiName} />
        {name}
        {showToggle && <MDIcon className={"toggle"} name={showToggle} />}
      </div>
    );
  }
};

class SideBar extends React.Component {
  constructor(props) {
    super(props);
    const { pathname } = props.location;
    this.state = {
      selectedSubMenu: null,
    };
    // Ensure that the appropriate sub-menu is open if the page is loaded to
    // a sub-entry
    for (let { name, subMenu } of Entries) {
      if (!subMenu) continue;
      if (subMenu.map(({ linkTo }) => linkTo).indexOf(pathname) !== -1) {
        this.state.selectedSubMenu = name;
        break;
      }
    }
    this.renderEntry = this.renderEntry.bind(this);
  }

  toggleState(name) {
    this.setState(previousState => ({
      selectedSubMenu: previousState.selectedSubMenu === name ? null : name
    }));
  }

  renderEntry({ name, subMenu, ...rest }) {
    const isSelectedSubMenu = this.state.selectedSubMenu === name;
    return (
      <div key={name} className={classnames("entry", isSelectedSubMenu && "selectedEntry")}>
        <SideBarLink
          name={name}
          onClick={() => this.toggleState(name)}
          showToggle={subMenu ? (isSelectedSubMenu ? "keyboard_arrow_down" : "keyboard_arrow_right") : null}
          {...rest}
        />

        {subMenu && isSelectedSubMenu && <div>{subMenu.map(SideBarLink)}</div>}
      </div>
    );
  }

  render() {
    return (
      <aside className={classnames("outer", this.props.isSideBarOpen === false && "hideSideBar")} >
        <div className={"overlay"} onClick={this.props.onToggle}></div>
        <Link className={"logoOuter"} to="/">
          <BHPLogo color="white" />
        </Link>

        <div className={"entries"}>{Entries.map(this.renderEntry)}</div>
      </aside>
    );
  }
}

export default withRouter(SideBar);



