import React from "react";
import { Link } from "react-router-dom";
import { withRouter } from "react-router";
import Entries from "../../router/config";

import "./styles.css";
class Breadcrumbs extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      pathname : this.createLink(props.location.pathname, Entries)
    }
    this.routerListener = props.routerListener;
    if (this.routerListener) {
      this.routerListener.listen((location, action) => {
        this.setState({ pathname : this.createLink(location.pathname, Entries)})
      });
    }
  }

  createLink(path, entries) {
    for (let { name, subMenu, linkTo } of entries) {
      if (name && name.length > 0) {
        if (linkTo && linkTo === path && !subMenu) {
          return <span>{name}</span>;
        } else if (subMenu) {
          let parent = name;
          for (let { name, linkTo } of subMenu) {
            if (linkTo === path) {
              return <span>{`${parent} / ${name}`}</span>;
            }
          }
        }
      }
    }
    return null;
  }

  render() {
    return (
      <div className={"breadcrumbs"}>
        <div className="container">
          <Link to="/">Home</Link>
          {" / "}
          {this.state.pathname}
        </div>
      </div>
    );
  }
}

export default withRouter(Breadcrumbs);



